import React, {useEffect} from 'react';
import './App.css';
import PaymentSection from './paymentSection';
// import firebase from './firebase';

const Sponsor = () => {

  useEffect(() => {        
    console.log(window.location.href);
    begin();    
  });

  async function begin() {
    console.log("starting up");
  }

  function submitCreditCard() {
    document.getElementById("creditCardDiv").style.display = "none";
    document.getElementById("nominatedAccount").style.display = "block";
  }

  function submitDonation() {

  }

  function donate(n) {
    document.getElementById("donationAmount").value = "$" + n;
  }

  return (
    <div className="Sponsor">
        <div className="content">
            <br />
            <h4>Sponsor a family</h4>
            <br />
            <div className="sponsorDiv">
                <table>
                    <tbody>
                        <tr>
                            <td>
                                <div className="card blueCard">
                                    <div className="card-body">
                                        <button onClick={() => donate(100)} className="btn btn-primary">Donate $100 a month</button>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div className="card orangeCard">
                                    <div className="card-body">
                                        <button onClick={() => donate(250)} className="btn btn-primary">Donate $250 a month</button>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div className="card greenCard">
                                    <div className="card-body">
                                        <button onClick={() => donate(500)} className="btn btn-primary">Donate $500 a month</button>
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>                                
            </div>
            <br />
            <div id="creditCardDiv" className="sponsorDiv">
                <h4>Your Credit Card Details</h4>
                <br />
                <PaymentSection />
                <button onClick={submitCreditCard} class="btn btn-primary" type="submit">Confirm details</button>
            </div>
            <div id="nominatedAccount" className="sponsorDiv" style={{display:"none"}}>
                <h4>Nominated Account Bank Details</h4>
                <br />
                <form>
                    <div className="form-row">
                        <div className="col-md-12 mb-3">
                            <label for="validationDefault01">Account Name</label>
                            <input type="text" className="form-control" id="validationDefault01" placeholder="Account Name" required />
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="col-md-12 mb-3">
                            <label for="validationDefault01">Account Number</label>
                            <input type="text" className="form-control" id="validationDefault02" placeholder="Account Number" required />
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="col-md-12 mb-3">
                            <label for="validationDefault01">Donation Amount (per month)</label>
                            <input type="text" className="form-control" id="donationAmount" placeholder="$0" required />
                        </div>
                    </div>
                    <button onClick={submitDonation} class="btn btn-primary" type="submit">Confirm details</button>
                </form>

            </div>
        </div>
    </div>
  );
}

export default Sponsor;
